<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-2 mt-2">
                <LeftColumn :HeaderText="$t('Channel')" />
            </div>
            <div class="col-10">
                <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                    IdentityColumn="id" :HeaderText="$t('AnnouncementMessage')" :ListData="AnnouncementMessageData" :HeaderData="filteredHeaders"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
                    SearchPosition="top" :SearchFields="topSearchFields" 
                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                
                    <template v-slot:slotdata="{ data }">
                        <!-- <template v-if="data.column.COLUMN_NAME == 'message'"> 
                            <div v-html="truncateMessage(data.row.message, 50)"></div>
                        </template> -->
                        <template v-if="data.column.COLUMN_NAME == 'status_id'"> 
                            <listing-status :isfor="'COMPANYSETUP_UPDATE'" v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')"
                                ref="updatestatus" v-bind:dataid="data.row.id" data-controller="COMPANYSETUP_UPDATE"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.row.id" data-action="index" module="AnnouncementMessage" subModule="AnnouncementMessage"
                                v-bind:dataselected="data.row.status_id.value == 1001 ? 'Active' : 'Inactive'"
                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                            </listing-status>
                        </template>

                        <template v-if="data.column.COLUMN_NAME == 'attachment'"> 
                            <listing-status :isfor="'COMPANYSETUP_UPDATE'" 
                                ref="updatestatus" v-bind:dataid="data.row.id" data-controller="COMPANYSETUP_UPDATE"
                                v-bind:item="data.row.id" data-action="index" module="AnnouncementMessage" subModule="AnnouncementMessage"
                                v-bind:callbackfunction="FetchData">
                            </listing-status>
                        </template>

                    </template>
                </tg-list>
            </div>
        </div>
        <!---modal popup-->
        <AddCompanySetup :recordId="recordId" :title="$t('Manage Announcement Message')" v-if="showAnnouncementMessagage"  v-on:close="showAnnouncementMessagage=false;recordId=0" v-on:addReloadData="FetchData();showAnnouncementMessagage=false;recordId=0" v-bind:moduleName="'Announcementmessage'" v-bind:emitName="'addReloadData'" v-bind:addApiUrl="'/Announcementmessage/add'" v-bind:subModule="'Announcementmessage'" />
    </div>
</template>
<script>
import AnnouncementMessageDataService from "@/services/DataService";
import Schema from "@/views/Company/AnnouncementMessage/AnnouncementMessageSchema";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        },
        configdata: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            recordId:0,
            noRecord:false,
            isLoading: false,
            CompanyId: null,
            AnnouncementMessageData: [],
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            topSearchFields: Schema.topsearchSchema,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            LeadStatusList: [],
            searchcondition: '',
            MODULENAME: null,
            SUBMODULECODE: null,
            privilegeParams: null,
            FileName: null,
            checkFileValidSize: false,
            checkFileValidExt: false,
            allowedPerFileSizeInMb: 0,
            disallowFileTypes: '',
            file: null,
            sharetype: '',
            // Headers: [],
            filteredHeaders: [],
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }                
            ],
            AttachmentData:[
            {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        }
                    ]
                }
            ],
            showAnnouncementMessagage:false
        }
    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        this.configData();
        ths.privilegeParams = `controller=Shift&action=`;
        await ths.FetchData();
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
        ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
            const filteredItem = { ...item };

            // Check if USER_EMP_ID is null or empty, then replace with '--'
            if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                filteredItem.USER_EMP_ID = '--';
            }

            return filteredItem;
            });
        },
        
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        
        renderActions: function (row) {
            console.log(row);
            // var self = this;
            // let rtnActionArray = [];
            // self.listActions.forEach(actionItem => {
            //     let action = { ...actionItem };
            //     switch (action.title) {
            //         case 'View':
            //             action.isVisible = row.ACCESS != null ? true : true;
            //             break;
            //         case 'Send Email':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Schedule':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Share':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Edit':
            //             action.isVisible = row.ACCESS != 1 ? true : false;
            //             break;
            //         default:
            //             action.isVisible = true;
            //             break;
            //     }
            //     rtnActionArray.push(action);
            // });
            // return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            console.log(event)
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;
                case "AddDepartment":
                    this.showAnnouncementMessagage = true
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'sharereport':
                    var vm = this;
                    AnnouncementMessageDataService.CheckPrivilege(vm.privilegeParams+'ShareReport').then((response)=>{
                        if(response.data){
                            vm.sharetype = callbackfunction
                            vm.folderid = item.FOLDER_ID;
                            vm.ReportID = item.REPORT_ID;
                            vm.foldername = item.REPORTNAME;
                            vm.showsharereportview = true;
                            $("#modalshareview").modal('show');
                            setTimeout(function () {
                                $("#modalshareview").modal('show');
                                setTimeout(function () {
                                    vm.SetBootstraptoolTip();
                                }, 200);
                            }, 1000);
                        }
                        else{
                            vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                            return false;
                        }
                    });
                    break;
                case 'sendemail':
                    vm.sendemail(item.REPORT_ID, item.MODULENAME, item.SUBMODULECODE);
                    break;
                case 'editreport':
                    vm.editreport(item);
                    break;
                case 'setschedule':
                    vm.setschedule(item.REPORT_ID);
                    break;
                case 'ViewReport':
                    vm.ViewReport(item.REPORT_ID, item.ACCESS, item.FOLDER_ID);
                    break;
            }
        },
        editreport: function (data) {
            var vm = this;
            AnnouncementMessageDataService.CheckPrivilege(vm.privilegeParams+'Update').then((response)=>{
                if(response.data){
                    vm.$router.push({
                        name: 'EditDataReport',
                        params: {
                            Modulename: data.MODULENAME, Submodulecode: data.SUBMODULECODE, ReportName: data.REPORTNAME, ReportID: data.REPORT_ID,
                            Module_Id: data.MODULE_ID, Folder_Id: data.FOLDER_ID, Sub_Module_Id: data.Sub_Module_Id, Version: vm.Version, Action: 'Edit'
                        }
                    });
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        ViewReport: function (ReportID, ACCESS, FolderID) {
            var vm = this;
            vm.$router.push('/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ReportID, ACCESS: ACCESS , Folder_Id: FolderID })))

        },
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            await this.FetchData();
        },
        ManageFolder: function () {
            var vm = this;
            AnnouncementMessageDataService.CheckPrivilege(vm.privilegeParams+'FolderListing').then((response)=>{
                if(response.data){
                    vm.ShowManageView = true;
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        $("#modalManageView").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                    vm.$refs.managefolder?.GetViewList();
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        AddDesignation: function () {
            var vm = this;
            console.log(vm, "Add");
        },
        commonDeleteData: function (event) {
            debugger;
            var vm = this;
            // AnnouncementMessageDataService.CheckPrivilege(vm.privilegeParams+'Delete').then((response)=>{
            //     if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "AnnouncementMessage",
                                subModuleCode: "AnnouncementMessage",
                                refCode: "COMPANYSETUP_DELETE"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            AnnouncementMessageDataService.CommonDeleteData(objectPostString).then(async function (response) {
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                    await vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                }

                            },);
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
                // }
                // else
                // {
                //     vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                //     return false;
                // }
            // })
        },
        UpdateStatus: async function (status) {
                        var vm = this;
                        if (status == 'Active') {
                            status = 1001;
                        } else {
                            status = 1002;
                        }
                        var val = "";
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                            if (val.length > 0) {
                                val += ',';
                            }
                            val += $(this).val();
                        });
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                                // Send IDs as a comma-separated string
                                var postJSON = {
                                    ID: val,
                                    StatusId: status,
                                    IsFor: "COMPANYSETUP_UPDATE",
                                    Module: "AnnouncementMessage",
                                    Submodule: "AnnouncementMessage",
                                };
            
                                var objectPostString = JSON.stringify(postJSON);
                                console.log(objectPostString);
                                AnnouncementMessageDataService.CommonUpdateStatus(objectPostString).then(response => {
                                    if (response.status == 200) {
                                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                        vm.FetchData();
                                    }
                                });
                            });
                        }
                    },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: function (row, column) {
            if(column.COLUMN_NAME == 'title'){
                this.recordId =row.id
                this.showAnnouncementMessagage = true
            }
            if (column.COLUMN_NAME == 'USERNAME') {
                this.$router.push({ name: 'viewcustomreport', params: { ReportID: row.REPORT_ID, ACCESS: row.ACCESS } });
            }
        },
        FetchData: async function () {
            // const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var vueObj = this;
            this.isLoading = true;
            var url=`CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}`;
            await AnnouncementMessageDataService.AnnouncementMessageListing(url)
                .then(function (response) {
                    vueObj.Headers = response.data.FieldSchema;
                    const filteredColumnNames = ["title", "message", "startdate", "enddate", "status_id"];
                    // Filter the original list based on the desired COLUMN_NAME values
                    vueObj.filteredHeaders =  vueObj.Headers.filter(obj => filteredColumnNames.includes(obj.COLUMN_NAME));
                    vueObj.filteredHeaders.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "title") {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter,
                            };
                        }                        
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    });
                    // &searchCondition=${vueObj.searchCondition}&isPaged=${vueObj.isPaged}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}
                    if (response.data.Data != "" && response.data.Data != null) {
                        if (response.data.Data.length > 0) {
                            vueObj.AnnouncementMessageData = response.data.Data;
                            vueObj.AnnouncementMessageData.forEach(item => {
                                item.created_by = JSON.parse(item.created_by);
                                item.modified_by = JSON.parse(item.modified_by);
                                item.status_id = JSON.parse(item.status_id);
                            });
                            
                            vueObj.TotalRecords = vueObj.AnnouncementMessageData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.AnnouncementMessageData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.AnnouncementMessageData = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
            });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                AnnouncementMessageDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        
        GetBase64String: function (evt) {
            try {
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },

        SizeToMb: function (bytes) {
            // debugger;
            var vueObj = this;
            var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
            if (bytes > sizeInBuytes) {
                return -1;
            }
            else {
                var byesInMB = (bytes / 1024) / 1024;
                return byesInMB;
            }
        },
        
        configData: function (value) {
            debugger;
            var vm = this;
            var data = vm.configdata;
            $.each(data, $.proxy(function (index1, item) {
                if (item.CORE_CONFIG_KEY == "TicketFileSize") {
                    this.allowedPerFileSizeInMb = item.CONFIG_DATA_VALUE;
                }
                else if (item.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                    this.disallowFileTypes = item.CONFIG_DATA_VALUE;
                }
            }, this));

        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },

        Close: function () {
            debugger;
            var vueObj = this;
            //vueObj._data.FormSchema[0].Data[0].value = null;
            vueObj.FileName = null;
            vueObj.file = null;
            vueObj.AttachmentData[0].Data[0].config.onChange = vueObj.GetBase64String;
            this.$emit("Close");           
            

        },
    }
}
</script>